// TypeScript 4.5 ts type 
import type { AxiosInstance, AxiosRequestConfig, AxiosResponse } from 'axios'
import axios from "axios"

class Http {
    instance: AxiosInstance
    config: AxiosRequestConfig
    constructor(option: AxiosRequestConfig) {
        this.config = option
        this.instance = axios.create(this.config)
        this.interceptors()
    }

    interceptors() {
        // 
        this.instance.interceptors.request.use(function (config) {
            // ,
            removePending(config)
            // 
            addPending(config)
            // 
            return config;
        }, function (error) {
            // 
            return Promise.reject(error);
        });

        // 
        this.instance.interceptors.response.use(function (response) {
            // 
            removePending(response.config)
            // 2xx 
            // 
            return response;
        }, function (error) {
            error.config && removePending(error.config)

            httpErrorStatusHandle(error)
            //  2xx 
            // 
            return Promise.reject(error);
        });
    }
    //  T  axios data
    request<T>(config: AxiosRequestConfig): Promise<AxiosResponse<T, any>> {
        return this.instance.request(config)
    }
}

const http = new Http({
    baseURL: `https://apis.netstart.cn/one/`,
    timeout: 120 * 1000
})

export default http

/**
 * 
 * @param {*} error
 */
function httpErrorStatusHandle(error: any) {
    // 
    if (axios.isCancel(error))
        return console.error("" + error.message);
    let message = "";
    if (error && error.response) {
        switch (error.response.status) {
            case 302:
                message = "";
                break;
            case 400:
                message = "";
                break;
            case 401:
                message = "";
                break;
            case 403:
                message = "";
                break;
            case 404:
                message = `: ${error.response.config.url}`;
                break; // 
            case 408:
                message = "";
                break;
            case 409:
                message = "";
                break;
            case 500:
                message = "";
                break;
            case 501:
                message = "";
                break;
            case 502:
                message = "";
                break;
            case 503:
                message = "";
                break;
            case 504:
                message = "";
                break;
            case 505:
                message = "HTTP";
                break;
            default:
                message = "";
                break;
        }
    }
    if (error.message.includes("timeout")) message = "";
    if (error.message.includes("Network"))
        message = window.navigator.onLine ? "" : "";
    // ui 
    console.error({
        type: "error",
        message,
    });
}

const pendingMap = new Map();
/**
 * cancel, 
 * @param {*} config
*/

function addPending(config: AxiosRequestConfig) {
    const pendingKey = getPendingKey(config);

    config.cancelToken =
        config.cancelToken ||
        new axios.CancelToken((cancel) => {
            // ancelToken cancel
            if (!pendingMap.has(pendingKey)) {
                pendingMap.set(pendingKey, cancel);
            }
        });
}

/**
 * 
 * @param {*} config
 */
function removePending(config: AxiosRequestConfig) {
    const pendingKey = getPendingKey(config);
    if (pendingMap.has(pendingKey)) {
        const cancelToken = pendingMap.get(pendingKey);
        // 
        cancelToken(pendingKey);
        // cancel
        pendingMap.delete(pendingKey);
    }
}


/**
 * key
 * @param {*} config
 * @returns
 */
function getPendingKey(config: AxiosRequestConfig) {
    const { url, method, params } = config;
    let { data } = config;
    if (typeof data === "string") data = JSON.parse(data); // responseconfig.data
    return [url, method, JSON.stringify(params), JSON.stringify(data)].join("&");
}